<template>
    <div id="wrapper">
        <navbar id="nav" class="no-print animate__animated animate__fadeInLeft"  style="display: none;"/>
        <div class="d-flex flex-column animate__animated animate__fadeInRight" id="content-wrapper" style="display: none !important;">
            <div id="content">
                <header-btn class="no-print"/>
                <div class="container-fluid text-dark">
                    <div class="row px-1 py-3 mb-1 no-print">
                        <label class="mt-1 col-xl-1 mb-2"> Month: </label>
                        <select class="form-control col-xl-1 col-md-4 mb-2" v-model="month">
                            <option value="1">1</option>
                            <option value="2">2</option>
                            <option value="3">3</option>
                            <option value="4">4</option>
                            <option value="5">5</option>
                            <option value="6">6</option>
                            <option value="7">7</option>
                            <option value="8">8</option>
                            <option value="9">9</option>
                            <option value="10">10</option>
                            <option value="11">11</option>
                            <option value="12">12</option>
                        </select>
                        <label class="mt-1 col-xl-1 mb-2"> Year: </label>
                        <input type="number" min="2020" class="form-control col-xl-1 col-md-4 mb-2" v-model="year">
                        
                        <button class="btn btn-success col-xl-2 col-md-4 mx-2 mb-2" v-on:click="search()"> Search <i class="fa fa-search"></i> </button><hr>
                    </div>
                    <div class="row px-1 py-3 mb-1 no-print">
                        <label class="mt-2 col-xl-1 mb-1"> 1$ = </label>
                        <input class="form-control col-xl-1 col-md-4 mb-2" v-model="dollar_price" type="number">
                    </div>
                    <hr class="no-print">
                    <button class="no-print btn btn-info btn-block mb-3" data-toggle="collapse" data-target="#staffs"> Staffs <i class="fa fa-arrow-down"></i> </button>
                    <div class="collapse" id="staffs">
                        <div class="row no-print">
                            <label class="col-sm-1 mt-1"> Staffs: </label>
                            <select v-model="staff_id" class="form-control col-sm-3 mb-2">
                                <option value=""></option>
                                <option v-for="staff in staffs" :key="staff.st_id" v-bind:value="staff.st_id"> {{ staff.staff_name }} </option>
                            </select>
                        </div>
                    </div>
                    <h3 class="mt-6"> Salary List For {{ month }}/{{year}} </h3>
                    <h4 class="mt-6"> Staff: {{ staff_id ? staffs.find(obj => obj.st_id == staff_id).staff_name : '' }} </h4>
                    <hr>
                    <table class="table table-bordered table-striped text-center text-dark">
                        <thead>
                            <th> Name </th>
                            <th> Daily </th>
                            <th> Days </th>
                            <th> Hours </th>
                            <th> Total Salary </th>
                            <th> Overtime Hours </th>
                            <th> Food </th>
                            <th> Transport </th>
                            <th> Cabina </th>
                            <th> Expense </th>
                            <th> Fine </th>
                            <th> Loan </th>
                            <th> Accomodation </th>
                            <th> Other Expense </th>
                            <th> Other Minus </th>
                            <th> Total (Dinar) </th>
                            <th> Total (Dollar) </th>
                        </thead>
                        <tbody>
                            <tr v-for="item in salary_list.filter(obj => obj.salary_type == 'Monthly')" :key="item.emp_id">
                                <td> {{ item.employee_full_name }} </td>
                                <td> {{ (getSuitableSalary(item)).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td> {{ item.count_present }} </td>
                                <td> {{ item.total_hour }} </td>
                                <td> {{ (getSuitableSalary(item) * item.count_present).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td> {{ ((item.daily_salary / 8 ) * item.total_o).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td> {{ (item.food_money).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td> {{ (item.transport_money).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td> {{ (item.cabina_money).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td> {{ (item.expense_money).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td> {{ (item.fine_money).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td> {{ (item.loan_money).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td> {{ (item.accomodation_money).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td> {{ (item.other_expense).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td> {{ (item.other_minus).toLocaleString() }} {{ item.salary_type == 'Monthly' ? '$' : '' }} </td>
                                <td>
                                    {{ Number(((getSuitableSalary(item) * item.count_present + (item.daily_salary / 8 ) * item.total_o + item.food_money + item.transport_money + item.cabina_money + item.expense_money + item.fine_money + item.loan_money + item.accomodation_money + item.other_expense + item.other_minus) * dollar_price).toFixed(3)).toLocaleString() }}
                                </td>
                                <td>
                                    {{ (getSuitableSalary(item) * item.count_present + (item.daily_salary / 8 ) * item.total_o + item.food_money + item.transport_money + item.cabina_money + item.expense_money + item.fine_money + item.loan_money + item.accomodation_money + item.other_expense + item.other_minus).toLocaleString() }}
                                    {{ item.salary_type == 'Monthly' ? '$' : '' }}
                                </td>
                            </tr>
                            <tr>
                                <td class="success--text"> Total Dollar </td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td class="success--text">
                                    {{ (getTotalDollar()).toLocaleString() }}
                                    $
                                </td>
                            </tr>

                            <tr v-for="item in salary_list.filter(obj => obj.salary_type == 'Daily')" :key="item.emp_id">
                                <td> {{ item.employee_full_name }} </td>
                                <td> {{ (item.daily_salary).toLocaleString() }}</td>
                                <td> {{ item.count_present }} </td>
                                <td> {{ item.total_hour }} </td>
                                <td> {{ (item.daily_salary * item.count_present).toLocaleString() }}</td>
                                <td> {{ ((item.daily_salary / 8 ) * item.total_o).toLocaleString() }}</td>
                                <td> {{ (item.food_money).toLocaleString() }}</td>
                                <td> {{ (item.transport_money).toLocaleString() }}</td>
                                <td> {{ (item.cabina_money).toLocaleString() }}</td>
                                <td> {{ (item.expense_money).toLocaleString() }}</td>
                                <td> {{ (item.fine_money).toLocaleString() }}</td>
                                <td> {{ (item.loan_money).toLocaleString() }}</td>
                                <td> {{ (item.accomodation_money).toLocaleString() }}</td>
                                <td> {{ (item.other_expense).toLocaleString() }}</td>
                                <td> {{ (item.other_minus).toLocaleString() }}</td>
                                <td>
                                    {{ ((item.daily_salary * item.count_present + (item.daily_salary / 8 ) * item.total_o + item.food_money + item.transport_money + item.cabina_money + item.expense_money + item.fine_money + item.loan_money + item.accomodation_money + item.other_expense + item.other_minus) ).toLocaleString() }}
                                    {{ item.salary_type == 'Monthly' ? '$' : '' }}
                                </td>
                                <td>
                                    {{ (((item.daily_salary * item.count_present + (item.daily_salary / 8 ) * item.total_o + item.food_money + item.transport_money + item.cabina_money + item.expense_money + item.fine_money + item.loan_money + item.accomodation_money + item.other_expense + item.other_minus)/ dollar_price).toFixed(3)).toLocaleString() }}
                                    {{ item.salary_type == 'Monthly' ? '$' : '' }}
                                </td>
                            </tr>
                            <tr>
                                <td class="success--text"> Total Dinar </td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                
                                <td class="success--text">
                                    {{ (getTotalDinar()).toLocaleString() }}
                                </td>
                                <td></td>
                            </tr>
                            <tr>
                                <td class="success--text"> Total Dinar and Dollar </td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                <td></td>
                                
                                <td class="primary--text">
                                    {{ (lastTotalDinar()).toLocaleString() }}
                                </td>
                                <td class="primary--text">
                                    {{ (lastTotalDollar()).toLocaleString() }} $
                                </td>
                                
                            </tr>

                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import auth from '../auth';
// import showError from '../showError.js';
export default {
    name: 'Salary_List',
    data() {
        return {
            month: new Date().getMonth() + 1,
            year: new Date().getFullYear(),
            staff_id: null,
            salary_list: [],
            staffs: [],
            dollar_price: 0,
            user: {
                username: '',
                role: '',
                en_id: null
            }
        }
    },
    beforeCreate(){
        auth("salaryList");
    },
    created(){
        this.$http.post("/getLoggedInfo").then(({data}) => {
            this.user.username = data.username;
            this.user.role = data.type;
            this.user.en_id = data.en_id || null;

            if(this.user.en_id != null){
                this.$http.post("/staff/getByEngineer/" + this.user.en_id).then(({data}) => {
                    this.staffs = data.filter(obj => obj.show_staff == '1');
                });
            } else {
                this.$http.post("/staff/getData").then(({data}) => {
                    this.staffs = data.filter(obj => obj.show_staff == '1');
                });
            }
        });
    },
   
    methods: {
        search() {
            this.$http.get('/employee/getSalaryListByMonthAndYear/' + this.month + '/' + this.year + '/' + this.staff_id).then(({data}) => {
                this.salary_list = data;
            });
        },
        getSuitableSalary(item) {
            var d = new Date();
            d.setMonth(this.month - 1);
            d.setFullYear(this.year);
            const allDaysInMonth = new Date(d.getFullYear(), d.getMonth() + 1, 0).getDate();
            const numberOfFridays = this.fridays(this.year, this.month - 1);
            const days = allDaysInMonth - numberOfFridays;
            if(item.salary_type == 'Monthly') {
                item.daily_salary = Number(item.monthly_salary / days).toFixed(2);
                return Number(item.monthly_salary / days).toFixed(2);
            } else {
                return (item.daily_salary);
            }
        },
        fridays(year, month) {
            var day, counter, date;
            day = 1;
            counter = 0;
            date = new Date(year, month, day);
            while (date.getMonth() === month) {
                if (date.getDay() === 6) { // Sun=0, Mon=1, Tue=2, etc.
                    counter += 1;
                }
                day += 1;
                date = new Date(year, month, day);
            }
            return counter;
        },
        getTotalDinar() {
            return this.salary_list.filter(obj => obj.salary_type == 'Daily').reduce((a, b) => a + (b.daily_salary * b.count_present + (b.total_o * b.daily_salary) + b.food_money + b.transport_money + b.cabina_money + b.expense_money + b.fine_money + b.loan_money + b.accomodation_money + b.other_expense + b.other_minus), 0)
        },
        getTotalDollar() {
            return this.salary_list.filter(obj => obj.salary_type == 'Monthly').reduce((a, b) => a + (this.getSuitableSalary(b) * b.count_present + (b.total_o * b.daily_salary) + b.food_money + b.transport_money + b.cabina_money + b.expense_money + b.fine_money + b.loan_money + b.accomodation_money + b.other_expense + b.other_minus), 0)
        },
        lastTotalDollar() {
            return this.getTotalDollar() + (this.getTotalDinar() / (this.dollar_price || 0));
        },
        lastTotalDinar() {
            return this.getTotalDinar() + (this.getTotalDollar() * (this.dollar_price || 0));
        }
    },
    watch: {
        staff_id() {
            this.search();
        }
    }
}
</script>

